/* 
throughout this stylesheet, Atomic CSS is used. 
Atomic CSS is the approach to CSS architecture that favors small, single-purpose classes with names based on visual function. 
Atomic CSS is used for colors, font styling, display properties, width, margins and padding. 

base classes are set for width, text alignment, margins and padding, but responsive classes are also set.


// MARGINS + PADDING 

shorthand for margins and padding follows the bootstrap 4 model

> m stands for margin and p stands for padding.

> p-0, p-md-0, p-lg-0, m-0, m-md-0, m-lg-0 completely remove ALL padding and margins. these are the most basic classes and do not have a location variable.

> location variables of l, r, t, b, x or y to differentiate where the margin or padding is going  - l is for left, r is right, t is top, b is bottom, x is along the x-axis and y is along the y-axis. 
  >> these location characters are the second value in the class name.

> auto values exist for all margins with location variables.
  >> .ml-auto, .mr-auto .mb-auto .mt-auto .mx-auto .my-auto;
  >> .ml-md-auto .mr-md-auto .mb-md-auto .mt-md-auto .mx-md-auto .my-md-auto
  >> .ml-lg-auto .mr-lg-auto .mb-lg-auto .mt-lg-auto .mx-lg-auto .my-lg auto

> All unit values for all classes are a value of either auto, 0, 1, 2, 3, 4 or 5. 
  >> These values relate to a number of rems. Auto values ONLY exist on margin classes.
  >> SASS variables are used to set the values for 1rem 2rem 3rem 4rem 5rem. When changing the value of SASS variables it will affect all of the margin and padding variables. These SASS variables are only used in the margin and padding classes.

>>> EXAMPLE - a base class may be ml-1 which means margin-left-1rem. 
  >> There is also ml-md-1 which is within a media query @ min-width 768px and stands for margin-left-medium-1rem.
  >> There are large margins and padding classes as well, and these come in at 1024px. an example of this is ml-lg-1 which is margin-left-large-1


//WIDTH

> width is set with .w-100, .w-75, .w-50, .w-25 and .w-20. 
  >> These set the values for the smallest screen sizes.
  >> Equivalent to width: 100%, 75%, 50%. 25%, or 20%

> .w-md-100 .w-md-75 .w-md-50 .w-md-25 and .w-md-20 come in at 768px.

> .w-lg-100 .w-lg-75 .w-lg-50 .w-lg-25 and .w-lg-20 come in a t 1024px.

// FONT

> classes .text-sm-left, .text-sm-right, .text-sm-center set the text alignment for small screen sizes

> .text-left, .text-right, .text-center set text alignment for 768px and up.

> .bold exists to set font-weight: $bold SASS variable.

// COLORS

> colors are set using the bootstrap model and the SASS color variables.
> do not have screen size options (no -md- or -lg-)

> a class exists for each SASS variable color in the color palette. These classes are equivalent to setting color: $variable;
  >> .purple, .magenta, .red, .coral, .orange, .dk-gray, .med-gray
  >> EXAMPLE: .purple{ color: $purple}

> a class for setting the background color for each SASS variable color in the color palette. These classes are equivalent to setting background-color: $variable;
  >> These follow the bootstrap model of using .bg-colorName, with .bg meaning background-color
  >> .bg-purple, .bg-magenta, .bg-red, .bg-coral, .bg-dk-gray, .bg-lt-gray
  >> EXAMPLE: .bg-purple{ background-color: purple;}

  >> .bg-transparent{ background-color: transparent;} is also available

// DISPLAY

> follows the bootstrap 4 model for flexbox properties

> .d-flex class equals .display: flex;
>> this does not have different options for screensizes.

> helper flex classes exist but also do not have options for screensizes.
  >> .align-items-center{ align-items: center;}
  >> .justify-items-center{ justify-items: center;}
  >> .align-content-center{ align-content: center;}
  >> .justify-content-center{ justify-content: center;}

*/

