//BUTTON STYLES + HOVER EFFECTS

.btn {
  color: $white;
  font-weight: bold;
  background-color: $coral;
  border: 1px solid $coral;
  padding: 1rem;
  font-size: 1.5rem;
  -webkit-transition: all .4s; /* Safari prior 6.1 */
  -o-transition: all .4s;
  transition: all .4s;
}

.btn:hover{
  opacity: .9;
  background-color: $coral;
  color: #fff;
  border: 1px solid $coral;
  border-radius: 5rem;
}

// Navbar overrides

#nav{
  border: 0px solid transparent;
  margin-bottom: 0px;
  position: relative;
  top: 7rem;
  z-index: 3;
}

.navbar > .container-fluid{
  padding-left: 0px;
  padding-right: 0px;
  // max-height: 100px !important;
  // border: 1px solid $dark-gray;
}

.navbar-header{
  margin-left: 0px !important;
  margin-right: 0px !important;
  padding: 1rem 0rem;
}

#brandHeader{
  position: absolute;
  left: 0rem;
  top: -6rem;
  z-index: 5;
  // margin-left: .1rem;
  padding: 0 0 0 0;
}

.navbar-brand{
  padding: 0 0 0 0;
}

#headImg{
  max-width: 300px;
  margin-left: 1rem;
}

#navbar-collapse{
  margin: 0 0 0 0;
  padding: 5rem 0 0 0;
}

.navbar-nav{
  padding-top: 3rem;
  padding-left: 5rem;
  max-width: 100%;
}

.navbar-nav > li{
  padding-bottom: 1rem;
}

.navbar-nav > li > a {
  color: $white !important;
  font-size: 1.7rem;
  padding-left: 5rem;
}

.navbar-toggle{
  // margin-right: 1rem;
  margin-top: .7rem;
  margin-bottom: .7rem;
  // border-color: $white !important;
}

.navbar-default .navbar-toggle .icon-bar{
  background-color: $white;
}

.navbar-default .navbar-toggle:hover, .navbar-default .navbar-toggle:focus{
  background-color: $magenta;
}

.navbar-default .navbar-nav > .open > a, .navbar-default .navbar-nav > .open > a:hover, .navbar-default .navbar-nav > .open > a:focus{
  color: $white;
  background-color: $magenta;
  -webkit-tap-highlight-color: $magenta;
}

.dropdown-menu{
  background-color: rgba(152, 38, 88, .8);
  top: 99%;
}

.navbar-default .navbar-nav .open .dropdown-menu > li > a{
  color: $white;
  font-size: 1.5rem;
}

.navbar-default .navbar-nav .open .dropdown-menu > li > a:hover {
  color: $white;
  background-color: rgba(152, 38, 88, .89);
}
.dropdown-menu > li > .subNav {
  padding-left:3.5rem !important;
}

.navbar-default .navbar-nav .open .dropdown-menu > li > a.first {
  font-size: 2rem; 
}

.caret{
  border: none;
  display: none;
}

.dropdown > .dropdown-toggle::after{
  font: normal normal normal 20px/1 FontAwesome;
  content: "\f078"!important; /* the desired FontAwesome icon */
  vertical-align: 0; /* to center vertically */
  font-size: 1rem;
}


